<template>
  <div>
    <el-form ref="defForm" :rules="rules" :model="defForm" label-width="80px">
      <el-form-item label="" prop="userName">
        <el-input v-model="defForm.userName"></el-input>
      </el-form-item>
      <el-form-item label="" prop="nickName">
        <el-input v-model="defForm.nickName"></el-input>
      </el-form-item>
      <el-form-item label="" prop="email">
        <el-input v-model="defForm.email"></el-input>
      </el-form-item>
      <el-form-item label="" prop="status">
        <el-select v-model="defForm.status" placeholder="">
          <el-option
              v-for="item in this.utils.status"
              :key="item.value"
              :value="item.value"
              :label="item.label">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item size="large">
        <el-button type="primary" @click="close"></el-button>
        <el-button type="primary" :loading="loading" @click="submitForm"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import {update} from '@/api/user'
export default {
  name: "Update",
  props: ['data'],
  created() {  // defForm
    // this.defForm = this.data  // props, this.defFormthis.data
    // 
    this.defForm = JSON.parse(JSON.stringify(this.data))
    console.log('--------')
    console.log(this.defForm)
    // 
  },
  data() {
    return {
      defForm: {
        id:'',
        userName: '',
        password: '',
        nickName: '',
        email: '',
        status: 'T',
      },
      loading: false,
      rules: {
        userName:[{required:true, message:''}],
        password:[{required:true, message:''}]
      }
    }
  },
  methods:{
    close() {
      this.$emit('hideDialog')
    },
    submitForm() {
      this.$refs['defForm'].validate(valid=>{
        if(valid) {
          this.loading=true
          // 
          update(this.defForm).then(data=>{
            console.log(data)
            this.loading=false // 
            this.$refs['defForm'].resetFields()  // 
            // 
            this.$emit('after')
            this.$emit('hideDialog')
          }).catch(error=>{
            this.loading=false
            console.log(error)
          })
        }
      })
    },
  }

}
</script>

