<template>
  <div>
    <div>
      <el-form ref="queryForm" inline="true" :model="queryForm" label-width="80px">
        <el-form-item>
          <el-input v-model="queryForm.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" icon="el-icon-search" @click="search"></el-button>
          <el-button type="success" icon="el-icon-plus" @click="append"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <el-table
        :data="tableData"
        stripe
        style="width: 100%">
      <el-table-column
          prop="id"
          label="ID"
          width="180">
      </el-table-column>
      <el-table-column
          prop="name"
          label=""
          width="180">
      </el-table-column>
      <el-table-column
          prop="single"
          label="">
      </el-table-column>
      <el-table-column
          prop="digest"
          label="">
      </el-table-column>
      <el-table-column
          prop="pos"
          label="">
      </el-table-column>
      <el-table-column
          prop="url"
          label="">
      </el-table-column>
      <el-table-column
          align="center"
          width="150px"
          label="">
        <template slot-scope="scope">
          <el-link @click="edit(scope.row)" :underline="false" type="primary" class="el-icon-edit-outline"></el-link>
          <el-link @click="del(scope.row)" :underline="false" type="danger" class="el-icon-delete"></el-link>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
        background
        layout="total, prev, pager, next, jumper"
        @current-change="handleCurrentChange"
        :current-page="pageNo"
        :total="total">
    </el-pagination>

    <!--editadd$emithideDialogeditVisiblefalse
              v-ifdomEdit, v-if="editVisible"  --->
<!--    <el-dialog :visible.sync="editVisible" v-if="editVisible" :close-on-click-modal="false">-->
<!--      <Edit @after="search" :data="formData" @hideDialog="hideDialog"></Edit>-->
<!--    </el-dialog>-->


  </div>
</template>

<script>
import {del, query} from "@/api/channel";

export default {
  name: "Index",
  components:'',
  data() {
    return {
      queryForm: {
        name: '',
        single: '',

      },
      tableData: [],
      total: 0,
      pageNo: 1,
      addVisible: false,
      editVisible: false,
      formData: {},
    }
  },
  mounted() { // 
    this.list({"page": this.pageNo})
  },
  methods: {
    list(param) {
      query(param).then(data => {
        this.tableData = data.list
        this.total = data.total
        console.log(data)
      }).catch(error => {
        this.$message.error(error)
      })
    },
    handleCurrentChange(val) { // 
      let param = this.queryForm
      this.pageNo = val
      param.page = this.pageNo
      this.list(param)
    },
    search() {
      let param = this.queryForm
      this.pageNo = 1
      param.page = this.pageNo
      this.list(param)
    },
    del(row) {
      this.$confirm('', '').then(() => {
        del(row.id).then(data => {
          console.log(data)
          let param = this.queryForm
          param.page = this.pageNo
          this.list(param)
        }).catch(error => {
          this.$message.error(error)
        })
      }).catch(error => {
        this.$message.error(error)
      })
    },
    append() {
      this.$router.push('/channel_add')
    },
    hideDialog() {
      // 
      this.editVisible = false
    },
    edit(row) {
      this.$router.push({
        path: '/channel_edit',
        query: {
          id: row.id
        },
      })
    }
  }
}
</script>

