<template>
  <div>
    <el-form ref="defForm" :rules="rules" :model="defForm" label-width="80px">
      <el-form-item label="" prop="tagName">
        <el-input v-model="defForm.tagName"></el-input>
      </el-form-item>
      <el-form-item size="large">
        <el-button type="primary" @click="close"></el-button>
        <el-button type="primary" :loading="loading" @click="submitForm"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import {create} from '@/api/tag'
export default {
  name: "Add",
  data() {
    return {
      defForm: {
          tagName: '',
      },
      loading: false,
      rules: {
        tagName:[{required:true, message:''}],
      }
    }
  },
  methods:{
    close() {
      this.$emit('hideDialog')
    },
    submitForm() {
      this.$refs['defForm'].validate(valid=>{
        if(valid) {
          this.loading=true
          // 
          create(this.defForm).then(data=>{
            console.log(data)
            this.loading=false // 
            this.$refs['defForm'].resetFields()  // 
            // 
            this.$emit('after')
            this.$emit('hideDialog')
          }).catch(error=>{
            this.loading=false
            console.log(error)
          })
        }
      })
    },
  }

}
</script>

